
* {
  margin: 0; 
  padding: 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
#top {
  width: 100%;
  height: 96px;
  background-image: -webkit-gradient(linear,left top, right top,from(#FF5E00),to(#FF5E00));
  background-image: linear-gradient(to right,#FF5E00,#FF5E00);
}
#top .logo {
  display: block;
  width: 200px;
  height: 75px;
  float: left;
  background:url(data:image/png;base64,) no-repeat top 28px left 15px;
  background-size: contain;
}
#top .download {
  position: absolute;
  right: 0;
  width: 118px;
  height: 34px;
  line-height: 34px;
  margin-top: 31px;
  border-radius: 20px;
  background: #fff;
  font-size: 12px;
  text-decoration: none;
  color: #FF5E00;
  margin-right: 10px;
}
#link {
  width: 100%;
  height: 48px;
  background: #F7F7F7;
  -webkit-box-shadow: 0 0 2px 2px #D4D4D4;
          box-shadow: 0 0 2px 2px #D4D4D4;
  background-image: -webkit-gradient(linear ,left top, left bottom ,from(rgba(249,249,249,.8)),to(rgba(235,235,235,.8)));
  background-image: linear-gradient(to bottom ,rgba(249,249,249,.8),rgba(235,235,235,.8));
}
#link ul {
  width: 100%;
  height: 46px;
}
#link li{
  list-style: none;
  float: left;
  width: 20%;
  height: 46px;
}
#link li a {
  display: block;
  width: 100%;
  line-height: 46px;
  font-size: 12px;
  color: #000;
  text-decoration: none;
  font-size: 16px;
}
#link .download {
  display: none;
  width: 100%;
  text-align: center;
  line-height: 46px;
  height: 46px;
}
.active {
  border-bottom: 2px solid #FF5800;
}
#link .active a {
  color: #FF5800;
}

footer {
  position: fixed;
  height: 72px;
  width: 100%;
  background:#ccc;
  bottom: 0;
}
footer .progress-bar {
  width: 100%;
  height: 2px;
  background: transparent;
}
.bar-in {
  width: 0%;
  height: 2px;
  background: #ff8500;
}
footer .img {
  padding: 6px;
  width: 60px;
  height: 60px;
  float: left;
}
footer .img img {
  width: 100%;
  height: 100%;
}
footer .info {
  float: left;
  padding: 10px;
  height: 60px;
  padding: 6px;
  font-size: 14px;
  text-align: left;
  color: #fff;
}
footer .info p:nth-of-type(1) {
  font-size: 18px;
}
footer .play,.pause {
  float: right;
  font-size: 30px;
  color: #FF8500;
}
.play {
  margin: 15px 30px 0 0;
}
.pause {
  margin: 15px 20px 0 0;
}

